import { setInterval, clearInterval } from 'timers';
  export default {
    name:'audioPlay',
    data () {
        return {
            isClick:false,
            counts:{
                m:'00',
                s:'00'
            },
            num:{
                m:'00',
                s:'00'
            },
            maxTime:0,
            nowTime:0,
            slider:0,
            t:null,
            mask:false,
            tipTxt:'?',
            isRead:false,
        }
    },
    props:{
        audioPath:{
            default:''
        },
        isCard:{
            default:0
        }
    },
    methods:{
        timer() {
            const that = this;
            let second = that.counts.s;
            let minute = that.counts.m;
            this.nowTime++
            if(this.nowTime>this.maxTime){//
                return false
            }
            second++
            this.slider=parseInt((this.nowTime/this.maxTime)*100);
            if (second >= 60) {
                second = 0  //  60
                minute++
                // if (minute >= 60) {
                //     minute = 0  //  60
                // }
                if (minute < 10) {
                // 10
                    that.counts.m='0'+minute;
                    that.counts.s=second;
                } else {
                    that.counts.m=minute;
                    that.counts.s=second;
                }
            }
            if (second < 10) {
                // 10
                that.counts.m=minute;
                that.counts.s='0'+second;
            } else {
                that.counts.m=minute;
                that.counts.s=second;
            }
        },
        readPlay(){//
            const maxTime=parseInt(this.$refs.test.duration);
            this.maxTime=maxTime;
            this.num.m=parseInt(maxTime/60);
            this.num.s=maxTime%60;
            if(this.num.m<10){
                this.num.m='0'+this.num.m
            }
            if(this.num.s<10){
                this.num.s='0'+this.num.s
            }
            this.isRead=true;
            
        },
        stop(){//
           let audio=this.$refs.test;
           audio.pause();
           audio.currentTime=0; 
           this.audioEnd();
        },
        pause(){//
            let audio=this.$refs.test;
            audio.pause();
            clearInterval(this.t);
            this.isClick=false;
        },
        playFn(){
            let audio=this.$refs.test;
            if(this.isClick){//
                audio.pause();
                clearInterval(this.t);
            }else{
                this.$emit('audioPlay');
                audio.play();
                this.t=setInterval(this.timer,1000);
            }
            this.isClick=!this.isClick
        },
        sliderChange(value){//change
            clearInterval(this.t);
            let audio=this.$refs.test;
            this.slider=value;
            this.nowTime=parseInt((this.maxTime*value)/100);
            this.counts.m=parseInt(this.nowTime/60);
            this.counts.s=this.nowTime%60;
            if(this.counts.m<10){
                this.counts.m='0'+this.counts.m
            }
            if(this.counts.s<10){
                this.counts.s='0'+this.counts.s
            }
            audio.currentTime = this.nowTime;
            this.t=setInterval(this.timer,1000);
        },
        deleteAudio(){
            this.$emit('deAudio');
        },
        tapDel(){
            this.mask=true;
        },
        audioEnd(){//
            clearInterval(this.t);
            this.counts={
                m:'00',
                s:'00'
            };
            this.t=null;
            this.nowTime=0;
            this.slider=0;
            this.isClick=false;
        }
    },
    created(){
        let u = navigator.userAgent;
        let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        if(isiOS){//IOScanplay
          this.isRead=true;
        }
    },
    mounted(){

    }
  }



